:root {
  --primary-red: hsl(1, 90%, 64%);
  --primary-blue: hsl(219, 85%, 26%);

  --neutral-white: hsl(0, 0%, 100%);
  --neutral-grayish-blue-200: hsl(210, 60%, 98%);
  --neutral-grayish-blue-400: hsl(211, 68%, 94%);
  --neutral-grayish-blue-600: hsl(205, 33%, 90%);
  --neutral-grayish-blue-800: hsl(219, 14%, 63%);
  --neutral-grayish-blue-900: hsl(219, 12%, 42%);
  --neutral-dark-blue: hsl(224, 21%, 14%);
}

@font-face {
  font-family: 'JakartaPlusSans';
  font-weight: 100 900;
  src: url(./assets//fonts/PlusJakartaSans-VariableFont_wght.ttf)
    format('truetype');
}
